.ProseMirror {
    padding: 10px;
    line-height: 1.6;
    border: 1px solid #ddd;
    border-radius: 8px;
    white-space: pre-wrap;
    word-wrap: break-word;
    min-height: 150px;
    background-color: #fff;
  }
  
  .ProseMirror pre {
    background: #f5f5f5;
    padding: 10px;
    border-radius: 4px;
    overflow-x: auto;
  }
  
  .ProseMirror code {
    background-color: #f5f5f5;
    padding: 2px 4px;
    border-radius: 4px;
  }
  